:root {
  --main-color: #9bb537;
  --primary-text-CH-color: #050505;
  --primary-text-CH-color-08: #050505cc;
  --primary-text-CH-color-05: #05050580;
  --primary-text-CH-color-03: #0505054d;
  --primary-text-CH-color-02: #05050533;
  --primary-text-CH-color-01: #0505051a;
  --primary-text-CH-color-RATING: #f8f8f8;
  --primary-text-CH-color-RATING-modal: #05050580;
  --primary-text-CH-color-STARS: #f8f8f81a;
  --primary-text-CH-color-CardTitle-Description: #f8f8f8;
  --primary-text-CH-color-menu-mob: #f8f8f8;
  --bgc-CH-background: #f8f8f8;
  --bgc-CH-background-mob-menu: #9bb537;
  --border-paginatio: #9bb537;
}

.link {
  text-decoration: none;
}

.nav-header {
  justify-content: space-between;
  align-items: center;
  padding-top: 25px;
  display: flex;
}

.logo-header {
  color: var(--main-color);
  text-align: center;
  letter-spacing: -.64px;
  font-size: 16px;
  font-weight: 600;
  line-height: 18px;
}

.logo-color {
  color: var(--primary-text-CH-color);
}

.header-btn-box {
  align-items: center;
  gap: 14px;
  display: flex;
}

.shopping-link {
  cursor: pointer;
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.shopping-icon {
  fill: #0000;
  stroke: var(--primary-text-CH-color);
}

.burger-menu {
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.burger-menu-icon {
  width: 32px;
  height: 32px;
  stroke: var(--primary-text-CH-color);
}

.switcher-input[type="checkbox"] {
  height: 0;
  width: 0;
  visibility: hidden;
  display: none;
}

.input-label {
  cursor: pointer;
  text-indent: -9999px;
  width: 46px;
  height: 20px;
  background: #cecdcd;
  border-radius: 18px;
  display: block;
  position: relative;
}

.input-label:after {
  content: "";
  width: 18px;
  height: 18px;
  background: #f8f8f8;
  border-radius: 18px;
  transition: all .3s;
  position: absolute;
  top: 1px;
  left: 1px;
}

.switcher-input:checked + .input-label {
  background: #9bb537;
}

.switcher-input:checked + .input-label:after {
  left: calc(100% - 1px);
  transform: translateX(-100%);
}

.input-label:active:after {
  width: 26px;
}

.menu-toggle {
  min-height: 40px;
  min-width: 40px;
  cursor: pointer;
  background-color: #0000;
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

@media (min-width: 768px) {
  .menu-toggle {
    display: none;
  }
}

.menu-toggle:hover, .menu-toggle:focus {
  background-color: #0000001a;
}

.menu-container {
  background-color: var(--bgc-CH-background-mob-menu);
  z-index: 999;
  padding: 200px 0 0;
  transition: transform .25s ease-in-out;
  position: fixed;
  inset: 0;
  transform: translateX(-100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.block {
  overflow: hidden;
}

.menu-container .menu-toggle {
  color: #fff;
  position: absolute;
  top: 16px;
  right: 16px;
}

.mobile-menu {
  margin: 0;
  padding: 0;
  list-style: none;
}

.mobile-menu .nav-link {
  color: #fff;
  text-align: center;
  padding: 0 50px 16px;
  text-decoration: none;
  display: block;
}

.theme-switcher.burger {
  position: absolute;
  bottom: 20px;
  left: 20px;
}

@media screen and (max-width: 767px) {
  .nav-list, .theme-switcher.main {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .nav-header {
    padding-top: 30px;
  }

  .burger-menu {
    display: none;
  }

  .theme-switcher {
