  visibility: visible;
  pointer-events: auto;
  background-color: var(--color-dark-green);
  transition: opacity var(--transform-hover-focus), visibility var(--transform-hover-focus);
  position: fixed;
  top: 0;
  left: 0;
}

.mobile-menu-container {
  padding-top: 29px;
  padding-bottom: 29px;
  position: relative;
}

.mobile-menu.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

