.swiper-hero {
  min-width: 870px;
  z-index: 1;
  border-radius: 15px;
}

.hero .swiper-pagination {
  margin-top: 14px;
  margin-left: 154px;
  position: relative;
  width: 350px !important;
  inset: auto !important;
}

.hero .swiper-pagination .swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  opacity: 1;
  background: none;
  justify-content: center;
  align-items: center;
  display: inline-flex;
  position: relative;
  margin: 0 !important;
}

.hero .swiper-pagination .swiper-pagination-bullet:before {
  content: "";
  width: 6px;
  height: 6px;
  background: var(--color-black-text);
  transition: var(--transform-hover-focus);
  border-radius: 50%;
  position: absolute;
}

body.dark-theme .hero .swiper-pagination .swiper-pagination-bullet:before {
  background: var(--color-light-theme);
}

.hero .swiper-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active:before, body.dark-theme .hero .swiper-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active:before {
  background: var(--color-green);
}

.hero .swiper-slide {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
  position: relative;
}

.event:before {
  content: "";
  width: 196px;
  height: 196px;
  background-color: var(--color-green);
  z-index: -1;
  opacity: .6;
  filter: blur(81px);
  display: block;
  position: absolute;
}

.event {
  z-index: 1;
  width: 350px;
  height: 442px;
